.header {
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-start;
  padding: 1vmin 0; }
  .header .logo a h3 {
    position: relative;
    font-size: 3.5vmin; }
    .header .logo a h3::before {
      content: url(./../../images/logo.png);
      display: inline-block;
      vertical-align: middle;
      font-style: normal; }
  .header .logo a p {
    position: relative;
    font-size: 2.5vmin; }
  .header .logo .morphing {
    position: relative;
    min-height: 3vmin;
    filter: contrast(25) blur(0.5px); }
    .header .logo .morphing .word {
      position: absolute;
      top: 0;
      right: 0;
      transform: translate(0, 0);
      -webkit-text-fill-color: aqua;
      animation: word 5s infinite ease-in-out; }
      .header .logo .morphing .word:nth-child(1) {
