      .header .logo .morphing .word:nth-child(2) {
        animation-delay: -3.33333s; }

@keyframes word {
  0%, 5%, 100% {
    filter: blur(0px);
    opacity: 1; }
  20%, 80% {
    filter: blur(1em);
    opacity: 0; } }

